import { VelteElement, VelteRender } from "velte";
import { router, query } from "./router";
import "./misc/theme";
import "./main.css";
import("preline");

//components
import Layout from "./components/layout";

//routes
import Home from "./routes/home";
import NotFound from "./routes/404";

//store
import { global } from "./store/global";
router.get("/", function (req, router) {
  renderPage(VelteElement(Home, null));
}).setName("Home");
router.notFoundHandler(function () {
  renderPage(Layout(VelteElement(NotFound, {
    $store: global
  })));
});
var renderPage = function renderPage(page) {
  VelteRender(page, document.getElementById("app"));
};
router.init();